'use strict';

(function (factory) {

    "use strict";

    if (typeof define === 'function' && define['amd']) {
        define(['knockout', 'text!./view.html', 'app/data-context-code', 'underscore', 'lib/knockout-bindings-datetime'], factory);
    }
})(function (ko, template, dataCtx, _) {

    "use strict";

    function ViewModel(params) {

        var self = this;

        self.repos = ko.observableArray([]);
        self.loading = ko.observable(true);
        self.error = ko.observable(false);
        self.hasRepos = ko.pureComputed(function () {

            return this.repos().length > 0;
        }, this);

        dataCtx.repos.get(100).then(function (repos) {

            self.repos(repos);
            self.loading(false);
        })['catch'](function (err) {

            self.loading(false);
            self.error(true);

            console.log('Error occured trying to get the repositories', err);
        });
    }

    return {
        viewModel: ViewModel,
        template: template
    };
});